@tailwind base;
@tailwind components;
@tailwind utilities;


/* Common product card styles */
.product-card {
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  text-align: center;
  width: 100%; /* Adjust width to 100% within the container */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  margin: 24px auto; /* Increase vertical margin for more spacing */
  box-sizing: border-box;
  position: relative; /* Ensure positioning for hover effect */
}

.product-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  margin-bottom: 16px;
  border-radius: 8px;
}

.product-card h3 {
  font-weight: 600;
  font-size: 1.2rem;
}

.product-card .price {
  color: red;
  font-weight: bold;
}

.product-card .old-price {
  text-decoration: line-through;
  color: #9e9e9e;
}

.product-card button {
  margin-top: 16px;
  background-color: black;
  color: white;
  padding: 8px 16px;
  border-radius: 8px;
  border: none;
  width: calc(100% - 32px); /* Adjust width to fit within the card's width */
  cursor: pointer;
  font-weight: bold;
  display: none; /* Hide button by default */
  position: absolute;
  bottom: -20px; /* Position slightly below the bottom border */
  left: 50%;
  transform: translateX(-50%);
}

.product-card:hover button {
  display: block; /* Show button on hover */
}

.rating-container {
  display: flex;
  align-items: center;
  margin-top: 8px;
}

.star {
  color: gold;
  margin-right: 4px;
}

.reviews {
  color: #4b5563;
  margin-left: 0.5rem;
}

/* Container styles for centering product rows */
.centered-container {
  width: 70%; /* Set the width to 70% */
  margin: 0 auto; /* Center the container */
  padding: 24px 0; /* Add vertical padding for more spacing */
}

/* Container styles for product rows */
.product-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 24px; /* Add bottom margin for more spacing */
}

.product-row .product-card {
  flex: 1 1 calc(25% - 16px); /* Adjust width to fit 4 cards per row with spacing */
  margin: 8px;
}

/* Pagination and button styles */
.pagination {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 8px; /* Reduce margin-top to bring buttons closer */
}

.pagination button,
.see-all-button {
  border: 1px solid #ccc; /* Lighter border color */
  color: #333; /* Darker text color */
  padding: 6px 12px; /* Reduce padding for a minimalist look */
  border-radius: 4px; /* Smaller border radius */
  background-color: transparent; /* Transparent background */
  cursor: pointer;
  font-weight: normal; /* Normal font weight */
  margin: 0 8px; /* Adjust margin to separate buttons slightly */
  transition: background-color 0.3s, color 0.3s; /* Smooth transition for hover effects */
}

.pagination button:hover,
.see-all-button:hover {
  background-color: #f0f0f0; /* Light grey background on hover */
  color: #000; /* Darker text color on hover */
}

.see-all-button {
  margin: 0 8px; /* Add margin to separate from other buttons */
}

/* BrowseByCategory.css */

.browse-by-category-container {
  margin: 2rem 0;
  padding: 24px 0; /* Add vertical padding for more spacing */
}

.browse-by-category-heading {
  font-size: 1.5rem;
  font-weight: 700;
  margin-bottom: 1rem;
  text-align: center;
}

.category-row {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-bottom: 24px; /* Add bottom margin for more spacing */
}

.category-card {
  flex: 1 1 calc(16.66% - 16px); /* Adjust width to fit 6 cards per row with spacing */
  margin: 8px;
  text-align: center;
  border: 1px solid #e0e0e0;
  border-radius: 8px;
  padding: 16px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  background-color: #fff;
  box-sizing: border-box;
}

.category-card img {
  width: 100%;
  height: 150px;
  object-fit: cover;
  margin-bottom: 16px;
  border-radius: 8px;
}

.category-card h3 {
  font-weight: 600;
  font-size: 1.2rem;
}